export default {
	data() {
		return {
			list:[],
			limit:10,
			page:1
		}
	},
	onLoad() {
		this.getList()
	},
	onReachBottom(){
		this.getList()
	},
	methods: {
		goDetail(info){
			console.log(info)
			info.content=encodeURIComponent(info.content)
			uni.navigateTo({
				url:"/pages/dojoDetail/dojoDetail?detail="+JSON.stringify(info)
			})
		},
		getList(){
			this.$api.get('/api/dojo',{
				params:{
					limit:this.limit,
					page:this.page
				}
			}).then((res)=>{
				if(res.data.length>0){
					this.list=this.list.concat(res.data)
					this.page++
				}
			})
		}
	}
}
